.catalog-page {
  @include flex();
  @include lg {
    display: block; }
  &__aside {
    width: 320px;
    padding-right: 10px;
    @include lg {
      position: fixed;
      top: $header-height-lg;
      z-index: 10;
      background-color: rgba($black, .7);
      bottom: 0;
      left: 0;
      right: 0;
      width: auto;
      padding: 0;
      @include hidden;
      @include ready {
        @include tr(opacity .3s, visibility .3s); }
      &.is-active {
        @include visible;
        .aside {
          transform: translate(0, 0); } } } }
  &__content {
    width: calc(100% - 440px);
    padding-left: 10px;
    margin-left: 120px;
    @include xl {
      margin-left: 0;
      width: calc(100% - 320px); }
    @include lg {
      width: auto;
      padding-left: 0; } }
  &__aside-toggle {
    display: none;
    padding-top: 25px;
    .burger {
      margin-right: 10px; }
    @include lg {
      display: flex;
      align-items: center; } } }


.catalog {
  &__top {
    @include flex(space-between, center);
    margin-bottom: 40px;
    @include xl {
      margin-bottom: 30px; }
    @include md {
      margin-bottom: 15px; } } }
