.aside {
  padding-top: 35px;
  padding-bottom: 25px;
  position: sticky;
  top: $header-height;
  @include lg {
    position: relative;
    top: auto;
    background-color: $white;
    height: 100%;
    width: 300px;
    padding: 15px;
    padding-top: 40px;
    overflow: auto;
    @include ready {
      @include tr(transform .5s $easeSmooth); }
    transform: translate(-100%, 0); }
  @include md {
    padding-top: 40px;
    padding-bottom: 15px; }
  &__title {
    @include lg {
      display: none; } }
  &__close {
    position: absolute;
    z-index: 1;
    top: 15px;
    right: 15px;
    display: none;
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    @include lg {
      display: block; } } }
