.custom-select {
  $this: &;
  position: relative;
  font-size: $fz-sm;
  width: 150px;
  select {
    position: absolute;
    @include hidden;
    width: 0;
    height: 0; }
  &.is-open {
    #{$this}__panel {
      @include visible; } }
  &.has-placeholder {
    #{$this}__opener {
      color: $gray; } }
  &__opener {
    position: relative;
    height: 48px;
    border: 1px solid  $gray;
    padding: 1em;
    padding-right: 2em;
    cursor: pointer;
    @include text-overflow;
    @include md {
      height: 40px;
      padding: .7em; }
    @include sm {
      height: 35px; }
    @include after {
      @include arr(10, 5, $c-text, b);
      display: block;
      position: absolute;
      z-index: 1;
      top: 50%;
      transform: translate(0, -50%);
      right: 1em; } }
  &__panel {
    position: absolute;
    top: 100%;
    left: 0;
    right: 0;
    z-index: 5;
    background-color: $white;
    border: 1px solid  $gray;
    border-top: 0;
    @include hidden;
    @include ready {
      @include tr(opacity .3s, visibility .3s); } }
  &__option {
    padding: .5em 1em;
    cursor: pointer;
    @include tr(color .3s);
    @include hover {
      color: $accent; }
    &.is-selected {
      color: $accent; }
    &[data-value="placeholder"] {
      display: none; } } }
