.form {
  &__captcha {
    @include flex(space-between, center);
    .input {
      margin-bottom: 0 !important;
      margin-right: 10px; } }
  &__field {
    @include notlast {
      margin-bottom: 1em; } }
  &__checkbox {
    font-size: $fz-sm;
    @include flex(space-between, center); }

  &--contacts {
    max-width: 775px;
    margin-left: auto;
    margin-right: auto;
    .input {
      @include notlast {
        margin-bottom: 1em; } } } }

.input, .textarea {
  font-size: $fz-sm;
  color: $article-text;
  input, textarea {
    background-color: $white-dark;
    border: 1px solid  $gray-light;
    height: 60px;
    padding: 19px 20px;
    width: 100%;
    @include tr(border-color .3s);
    @include placeholder {
      color: $gray; }
    @include sm {
      height: 50px;
      padding: 12px; }
    &:focus {
      border-color: $article-text; } }
  &--short {
    width: 260px; }
  &--full-h {
    height: 100%;
    input, textarea {
      height: 100% !important;
      @include xs {
        min-height: 135px; } } } }
