.news-item {
  display: block;
  @include cover;
  background-color: $gray-light;
  color: $white;
  padding: 35px;
  position: relative;
  padding-top: 40%;
  @include tr(color .3s);
  @include before {
    @include coverdiv;
    background-color: rgba($black, .5);
    z-index: 0; }
  @include hover {
    color: $accent; }
  @include lg {
    padding: 25px;
    padding-top: 40%; }
  @include md {
    padding: 15px;
    padding-top: 50%; }
  @include sm {
    padding-top: 40%; }
  @include xs {
    padding-top: 50%; }
  &__upttl {
    color: #d3d5d2;
    margin-bottom: 10px;
    display: block; }
  &__content {
    position: relative;
    z-index: 1;
    display: block; }
  &__title {
    display: block; } }
